var browser = {
  versions: function () {
   var u = navigator.userAgent, app = navigator.appVersion;
   return {   //移动终端浏览器版本信息
    trident: u.indexOf('Trident') > -1, //IE内核
    presto: u.indexOf('Presto') > -1, //opera内核
    webKit: u.indexOf('AppleWebKit') > -1, //苹果、谷歌内核
    gecko: u.indexOf('Gecko') > -1 && u.indexOf('KHTML') == -1, //火狐内核
    mobile: !!u.match(/AppleWebKit.*Mobile.*/), //是否为移动终端
    ios: !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/), //ios终端
    android: u.indexOf('Android') > -1 || u.indexOf('Linux') > -1, //android终端或uc浏览器
    iPhone: u.indexOf('iPhone') > -1, //是否为iPhone或者QQHD浏览器
    iPad: u.indexOf('iPad') > -1, //是否iPad
    webApp: u.indexOf('Safari') == -1 //是否web应该程序，没有头部与底部
   };
  }(),
  language: (navigator.browserLanguage || navigator.language).toLowerCase()
};

module.exports = {
  'PLAY_AUDIO':{
    methods:{
      playAudio(index){
        var hash = this.songList[index].hash
        var info = {
          list: this.songList,
          index: index
        }
        var songList = this.$store.state.listInfo.songIndex;
        this.$store.commit('setListInfo', info);
        this.$store.dispatch('getSong', hash); 
        if(songList != index){
          document.getElementById('audioPlay').load();
        }
      },
			download(index){
        if (browser.versions.mobile) {//判断是否是移动设备打开。browser代码在下面
          var ua = navigator.userAgent.toLowerCase();//获取判断用的对象
          if (ua.match(/MicroMessenger/i) == "micromessenger") {
            alert("当前在微信内打开，无法下载！请换成系统自带浏览器或者其它浏览器。");//在微信中打开
            return ;
          } else if (ua.match(/QQ/i) == "qq") {
            alert("当前在QQ内打开，无法下载！请换成系统自带浏览器或者其它浏览器。");//在QQ空间打开
            return ;
          } else if (ua.match(/WeiBo/i) == "weibo") {
            alert("当前在微博内打开，无法下载！请换成系统自带浏览器或者其它浏览器。");//在新浪微博客户端打开
            return ;
          }      
        }
				this.$http.get(`/bproxy/yy/index.php?r=play/getdata&hash=${this.songList[index].hash}`).then(({data}) => {
					var songInfo = data.data;
          this.$downloader.saveAs("/downloadProxy"+ songInfo.play_url, songInfo.audio_name+ ".mp3");
				})
			}
    }
  }
};



